import { adapters } from "@rp/adapters"
import {
  ChatInputResizeInteractionMessage,
  ChatLeaveWhileAnswerNotCompletedInteraction,
  ChatShareInteractionMessage,
  ExternalToolsAgentChatFeedbackMessage,
  ExternalToolsAgentChatInteractionMessage,
  ExternalToolsAgentCopyResponseMessage,
  ExternalToolsChatAuditMessage,
  ExternalToolsChatFeedbackMessage,
  ExternalToolsChatInteractionMessage,
  ExternalToolsChatSourceInteractionMessage,
} from "./customerIOChatEvents.types"

export const sendChatFeedback = (data: ExternalToolsChatFeedbackMessage["data"]) => {
  const message: ExternalToolsChatFeedbackMessage = {
    name: "chatFeedback",
    data,
  }
  adapters.customerIo.track(message)
  return message
}

export const sendChatInteraction = (data: ExternalToolsChatInteractionMessage["data"]) => {
  const message: ExternalToolsChatInteractionMessage = {
    name: "chatInteraction",
    data,
  }
  adapters.customerIo.track(message)
  return message
}

export function sendAgentChatInteraction(params: {
  chatId: string
  interactionTimestamp: string | null
  executionMode: "chat" | "research"
  requestId: string
}) {
  const message: ExternalToolsAgentChatInteractionMessage = {
    name: "agentInteraction",
    data: {
      chatId: params.chatId,
      interactionTimestamp: params.interactionTimestamp,
      executionMode: params.executionMode,
      requestId: params.requestId,
    },
  }
  adapters.customerIo.track(message)
  return message
}

